* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #eee;
}

.container {
  max-width: 800px;
  position: relative;
  min-width: 500px;
  margin: 0 auto;
  background-color: #fff;
}

.header {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #999;
}

.main {
  display: grid;
  grid-template-columns: 300px 1fr;
}

.aside {
  height: 100vh;
  border-left: 1px solid #ccc;
  box-shadow: 0 0 5px rgba(3, 3, 3, 0.5);
  transition: opacity 0.25s ease-out;
  opacity: 1;
}

.section {
  height: 100%;
}

.footer {
  width: 100%;
  max-width: inherit;
  height: 50px;
  position: fixed;
  bottom: 0;
  border-top: 1px solid #999;
}

@media screen and (max-width: 700px) {
  .aside {
    opacity: 0;
  }
}
